@import "../base";

#youtubeplayer {
  > div {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
    iframe {
      position: absolute;
      top: 0;
      right: 0;
      width: 100% !important;
      height: 100% !important;
    }
  }
}

.player-title {
  color: map-get($theme-colors, white100);
  display: flex;
  justify-content: space-between;
  align-items: center;
  h5 {
    margin-right: 12px;
    font-size:3vh;
    height:3.5vh;
    line-height: 1.16;
    width: calc(100% - 40px);
    overflow: hidden;
    position: relative;
    span{
      position: absolute;
      display : inline-block;
      padding-left: 100%;
      white-space : nowrap;
      animation   : scrollAnime 15s linear infinite;
    }
    @keyframes scrollAnime{
        0% { transform: translateX(0)}
      100% { transform: translateX(-100%)}
    }
  }
}


.controls{
  display: flex;
  align-items: center;
  &__play-section{
    flex: 1;
    padding: 0 8px;
  }
}

.play-section{
  display: flex;
  justify-content: space-between;
  &__start,&__end{
    flex: 1;
    margin: 4px;
    input{
      position: relative;
      width: 100%;
      box-sizing: border-box;
      text-align: right;
      background: #27302F;
      padding: 0.5em 0.5em 0.5em 1em;
      color:#FFF;
      border:none;
      outline:0;
      border-radius: 5px;
      //font-family: 'Noto Sans JP', sans-serif;
      &:before{
        position: absolute;
        color:#636969;
      }
    }
  }
  &__start{
    input:before{
      content:"start";
    }
  }
  &__end{
    input:before{
      content:"end";
    }
  }
}
